#loading {
  -webkit-transition: .8s;
  -o-transition: .8s;
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 99999;
  transition: .8s;
  background: #fff;
  width: 100%;
  height: 100%; }
  #loading #progress {
    display: none;
    transition: .5s;
    background: #000;
    width: 0;
    height: 1px; }
  #loading #progress, #loading .progstat-wrapper {
    position: absolute;
    top: 50%;
    transform: translateY(-50%); }
  #loading .progstat-wrapper {
    margin-top: 0;
    width: 100%;
    color: #000;
    font-size: 14px;
    font-weight: 300;
    letter-spacing: 3px;
    text-align: center; }
  #loading .logo-loader {
    transform: translate(-50%, -50%);
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -100px;
    width: 150px;
    height: 150px; }

